import { CqsChunk } from "@rp/common/api-types"
import { BriefSources } from "@rp/react/tanstack-api/brief"

type Source = BriefSources[string]

const transformSourceIntoChunk = (source: Source): CqsChunk => {
  return {
    cnum: source.chunkId,
    entities: [],
    deduplicationCount: 0,
    label: source.headline,
    sentences: source.highlights,
    sentiment: 0,
    text: source.text,
  }
}

export { transformSourceIntoChunk }
